import "./Styles/ChooseUs.scss";
import BsVideo from "../Videos/BsVideo.mp4";

const ChooseUs = () => {
  return (
    <>
      <section className="container " id="ChooseUs">
        <section className=" ChooseUSMainSection">
          <article className="col-lg-7 col-sm-12 ">
            <h2>Why Choose Us ?</h2>
            <h3>Because professionals choose us!</h3>
            <ul>
              <li className="ChooseUsList">
                Bollywood resides in every Indians heart.
                <br />
                And, we wouldnt want to miss an opportunity to say,Naam toh
                suna hi hoga!
              </li>
              <li className="ChooseUSList">
                Do you want to see your brand reach new heights and stand out?
                Well, then we would be the perfect fit for it! We never fake our
                promises. Our commitment is our strength. You can trust us, and
                in turn, you get your work done on time. Do you want to know how
                our BS group helps you?
                <br />
                Well, that's quite simple! We cater to a larger audience and
                make you be the shining star among all
              </li>
              <li className="ChooseUSList">
                Our team believes in Content perfect nahi, attractive honi
                chahiye!
              </li>
              <li className="ChooseUSList collapse" id="DownData" >
                Creating content is an art, and we acknowledge that
                wholeheartedly, we make your work unique in every possible way.
                However, we know how to help you out, and we assure you that you
                can consider yourself free after choosing us.
              </li>
              <li className="ChooseUSList collapse" id="DownData">
                We understand that every client has a different point of view
                and opinion regarding what they're looking for, and that's what
                we are here for. We want to share your story and experience and
                let the world know about it.
              </li>
              <li className="ChooseUSList collapse" id="DownData">
                After all, it's high time for people to know that doctors and
                engineers are not the only careers on the planet. It's time to
                set the bars for creativity!
              </li>
              <li className="ChooseUSList collapse" id="DownData">
                Professionalism is the key to success. That's why we have
                numerous departments dedicated to excelling in the work to serve
                you. In this way, we can ensure the result is creative for you
                so that you can always be the best player assuring you the best
                quality.
              </li>
              <li className="ChooseUSList collapse" id="DownData">
                We even have trained professionals working in the same lines,
                and they know the secret behind success.
              </li>
              <li className="ChooseUSList collapse" id="DownData">
                We want you to succeed, and that's what our endgame is. Whether
                it's a high-end company or taking your way off, we are going to
                be with you and there for you and through thick and thin,
                ensuring you grow every single day when you are with us!
              </li>
              <li className="ChooseUSList collapse" id="DownData">
                Toh kya khayal hai, Jeet ki tayari shuru ki jaaye?
              </li>
            </ul>
            <button className="DiscoverMoreBtn" data-bs-toggle="collapse"
                data-bs-target="#DownData">Discover More</button>
          </article>
          <figure className="col-lg-5 col-sm-12 ">
            <video controls autoPlay>
              <source src={BsVideo} type="video/mp4" />
            </video>
          </figure>
        </section>
      </section>
    </>
  );
};
export default ChooseUs;
